import { get } from 'lodash-es';
import { styled, css } from '@mui/material/styles';
import { Tabs as MuiTabs, Tab as MuiTab, Box as MuiBox } from '@mui/material';
import { numberToPx } from '../../../utils/helpers';
export const Tabs = styled(MuiTabs, {
  shouldForwardProp: propName => !['wrap', 'reverse', 'fillActiveTab', 'customColor'].includes(propName)
})`
    padding: 0;

    & .MuiTabs-indicator {
        background-color: ${props => props.customColor};
    }

    & .MuiTab-root {
        position: relative;

        &.Mui-selected {
            ${props => {
  const color = props.customColor ?? get(props, `theme.palette.primary.main`);
  const borderLeft = props.orientation === 'vertical' && props.reverse;
  return props.fillActiveTab ? css`
                          background-color: ${color};
                          color: #ffffff; // TODO: GET CONTRAST COLOR
                      ` : css`
                          color: ${color};
                      `;
}};
            ${props => {
  return props.orientation === 'vertical' ? css`
                          border-left: ${props.reverse ? '2px solid' : 'unset'};
                          border-right: ${props.reverse ? 'unset' : '2px solid'};
                      ` : css``;
}};

            ${props => {
  const color = props.customColor ?? get(props, `theme.palette.primary.main`);
  return props.wrap ? css`
                          ::after {
                              position: absolute;
                              content: ' ';
                              background-color: ${color};
                              ${props.orientation === 'vertical' ? {
    left: props.reverse ? 0 : 'unset',
    right: props.reverse ? 'unset' : 0,
    width: '2px',
    height: '100%'
  } : {
    bottom: 0,
    width: '100%',
    height: '2px'
  }}
                          }
                      ` : css``;
}}
        }
    }
`;
export const Tab = MuiTab;
export const TabPanel = styled(MuiBox, {
  shouldForwardProp: propName => !['iconPosition', 'disableRipple'].includes(propName)
})`
    width: 100%;
    height: 100%;
    padding: 0;
    flex-grow: 1;
`;
export const Box = MuiBox;
export const TabWrapper = styled(MuiBox, {
  shouldForwardProp: propName => !['reverse', 'orientation', 'verticalMaxFixedHeight'].includes(propName)
})`
    width: 100%;
    padding: 0;
    display: flex;
    flex-direction: ${props => props.reverse ? 'column-reverse' : 'column'};
    ${props => {
  if (props.orientation === 'vertical') {
    return css`
                flex-direction: ${props.reverse ? 'row-reverse' : 'row'};
                max-height: ${numberToPx(props.verticalMaxFixedHeight) ?? 'inherit'};
            `;
  }
}}
`;